.navbar{
    display: flex;
    background-color: #222;
    justify-content: space-between;
    padding-right: 10px;
}

.navbar img{
    padding: 10px 0px 10px 0px; 
}

.list{
    display: flex; /*deixar itens lado a lado*/
    list-style: none; /*remover a bolinha do lado das listas*/
    align-items: center; 
}

.item{
    margin-right: 1em; 
    padding: 10px;
}

.item a{
 color: #fff;
 text-decoration: none;
 font-size: 18px;
 font-weight: bold;
}

.item a:hover{
 color: #ffbb33;
 
}