.container{
    width: 1200px;
    display: flex;
    justify-content: space-between; /*espacar os elementos de forma igual na horizontal*/
    margin: 0 auto; /*contralizar o conteudo na tela*/
    flex-wrap: wrap; /*se o conteudo ultrapassar 1200px ele sera passado para a proxima linha*/
}

.min-height{
    min-height: 75%; /*vai definir uma altura minima de 75%*/
}

.start{
    justify-content: flex-start; /*vai substituir o justify content acima quando necessario*/
}

.column{
  flex-direction: column;
  justify-content: flex-start;
}
