footer{
   background-color: #222;
   color:#fff;
   padding: 3em;
   display: flex;
   align-items: center;
   flex-direction: column;
}

footer ul{
    display: flex;
    list-style-type: none;
    justify-content: center;
    
}

footer li{  
    margin: 0px 12px;
}

footer svg{
    font-size: 1.5em;
    color: #fff;
    width: 100%;
    transform: scale(0.9);
    transition: 0,2s;
}
footer svg:hover{
    color:#ffbb33;
    transform: scale(1)
}


.copyright{
    font-weight: bold;
    margin-top: 12px;;
}
footer a{
    color: #ffbb33;
}